.info-container {
    margin: auto;
    width: 25rem;
    height: fit-content;
}

.info-aesthetic {
    font-size: 2rem;
    font-family: 'Playfair Display', serif;
    font-weight: 900;
    color: rgb(136 136 136 / 62%);
}

.info-title-container {
    width: 100%;
    height: fit-content;
    margin: 1rem 0;
}

.info-title {
    font-size: 5rem;
    font-family: 'Raleway', sans-serif;
    font-weight: 600;
}

.info-text-section {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    height: fit-content;
}

.indent-line-container {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    height: 2rem;
}

.indent-line {
    width: 3rem;
    height: 0.05rem;
    background-color: #5c5c5c;
    margin-right: 1rem;
}

.info-text-container {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: fit-content;
}

.info-text {
    font-size: 2rem;
    font-family: 'Raleway', sans-serif;
    font-weight: 200;
    width: 100%;
}

@media (max-width: 1150px) {
    .info-container {
      width: 40rem;
      margin: 0;
      margin: 3rem auto;
    }
}

@media (max-width: 700px) {
    .info-container {
      width: 28rem;
    }
}

@media (max-width: 500px) {
    .info-container {
      width: 100%;
      margin-bottom: 1.55rem;
      padding: 0 1rem;
    }
    .info-title {
        font-size: 3rem;
    }
    .info-text {
        font-size: 1.2rem;
    }
}
    