.footer-container {
    width: 100%;
    height: 3.5rem;
    background-color: #5c5c5c;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.footer-title {
    color: #fff;
    font-family: 'Raleway', sans-serif;
    font-weight: 100;
    font-size: 1.5rem;
}


@media (hover: hover) {
    .footer-container {
        position: fixed;
        bottom: 0;
        z-index: 2;
    }
}

@media (max-width: 500px) {
    .footer-container {
        position: fixed;
        bottom: 0;
    }
    .footer-title {
        font-size: 1rem;
    }
}